import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { ProfileNewComponent } from './profile-new/profile-new.component';

import { PrivillageAccess } from '../../@core/services/_privillageguard.service';
import { ACC_SETTING } from '../../../constant';

const routes: Routes = [
  {
    path: '',
    children: [
      {
        path: 'user-profile',
        component: ProfileNewComponent,
        canActivate: [PrivillageAccess],
        data: {
          pri: ACC_SETTING,
        },
      },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class UserAccountRoutingModule {
}
