
import { Component, OnInit, Input, Output, TemplateRef, EventEmitter } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { NbDialogService } from '@nebular/theme';
import { EncriptionService, PackageService } from "../../../../@core/services";

import { ToastrComponent } from '../../../../@core/components';
import { ProfileService } from '../../../../@core/services';
import { environment } from '../../../../../environments/environment';
import { CommonModule } from '@angular/common';

@Component({
  selector: 'ngx-genaral-details',
  standalone: true,
  imports: [CommonModule],
  templateUrl: './genaral-details.component.html',
  styleUrl: './genaral-details.component.scss'
})
export class GenaralDetailsComponent {
  @Input() details: any = null;
  @Output() getMerchantDetails: EventEmitter<any> = new EventEmitter();

  is_requesting: boolean = false;
  modal_ref: any = null;
  profile_pic_form: FormGroup = null;
  image_base_path = environment.aws;
  is_loading: boolean = false;
  package_details: any;

  userDetails = {
    name: 'Spemai (PVT) LTD',
    address: '292,Gamsabha Junction,High-level Rd,Nugegoda',
    registration_no: 'PV00206829',
    contact_person_name: 'Mr.Amila Fernando',
    contact_number: '+94117021540',
    designation:'CEO'
  };

  constructor(private fb: FormBuilder, private dialogService: NbDialogService, private toastr: ToastrComponent, private profileService: ProfileService, private packageService: PackageService,
    private encriptionService: EncriptionService) {
    this.profile_pic_form = this.fb.group({
      picture: ['', [Validators.required]],
    });
  }
  get picture() {
    return this.profile_pic_form.get('picture');
  }



  ngOnInit(): void {
    this.getCurrentPackageDetails()
   }
  onKeyDown(event: any) {
    const key_cord = event?.keyCode;
    if(key_cord === 17 || key_cord === 16 || key_cord === 35 || key_cord === 36 || key_cord === 37 || key_cord === 39 || key_cord === 67) {
      return true;
    }
    return false;
  }
  formatCommisionRate(rateString: string) {
    return parseFloat(rateString) && rateString;
  }
  getCurrentPackageDetails() {
    this.is_loading = true;
    const user_id = this.encriptionService.response_decript(
      localStorage.getItem("xu")
    )?.user_id;

    this.packageService.getCurentPackageDetails(user_id).then(
      (res: any) => {
        this.is_loading = false;
         console.log('getCurrentPackageDetails', res);

        if (res && res?.status === 100) {
          this.package_details = res?.data;
        } else {
          this.toastr.showToast(
            "danger",
            "Error!",
            res?.message || "Getting package details failed."
          );
        }
      },
      (error: any) => {
        this.is_loading = false;
        console.log("Internal Server Error", error);
        this.toastr.showToast("danger", "Error!", "Internal Server Error.");
      }
    );
  }
  
  addUpdateProfilePic() {
    this.is_requesting = true;

    const body = {
      merchant_logo: this.picture.value,
    };
    console.log(body,'image')
    this.profileService.updateProfilePic(body).then((res: any) => {
      // console.log('addUpdateProfilePic', res);
      this.is_requesting = false;

      if (res?.status === 100) {
        this.toastr.showToast("success", "Success!", `Profile picture ${this.details?.merchant_logo ? 'Updated' : 'Added'} successfully.`);
        this.cancel();
        setTimeout(() => {
          this.getMerchantDetails.emit();
        }, 500);
      }
      else {
        this.toastr.showToast('danger', 'Error!', res?.message || `${this.details?.merchant_logo ? 'Updating' : 'Adding'} profile picture failed.`);
      }
    }, (error: any) => {
      this.is_requesting = false;
      console.log('Internal Server Error', error);
      this.toastr.showToast('danger', 'Error!', 'Internal Server Error.');
    });
  }
  isFieldInvalid(fieldName: string) {
    const fieldObj = this.profile_pic_form.get(fieldName);

    if(fieldObj && fieldObj?.invalid && fieldObj?.errors && (fieldObj?.dirty || fieldObj?.touched)) {
     return true;
    }

    return false;

  }

 

  /*
  on selected profile pic changes
  */
  onFileChange(file: any) {
    // console.log(file);
    
    if(!file || Array.isArray(file) || typeof file !== 'object') {
      this.picture.setValue('');
      return;
    }

    const render = new FileReader();
    render.readAsDataURL(file);
    render.onload = () => {
      this.picture.setValue(render.result);
    }

  }
  openModal(modal: TemplateRef<any>) {
    this.modal_ref = this.dialogService.open(modal, { closeOnBackdropClick: false });

  }

  cancel() {
    this.profile_pic_form.reset();
    this.modal_ref.close();
  }

}
