import { Component, OnInit, Input, Output, TemplateRef, EventEmitter } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ReactiveFormsModule } from '@angular/forms';
import { CommonModule } from '@angular/common';
import { NbDialogService } from '@nebular/theme';

@Component({
  selector: 'ngx-business-details',
  standalone: true,
  imports: [ReactiveFormsModule,CommonModule],
  templateUrl: './business-details.component.html',
  styleUrl: './business-details.component.scss'
})
export class BusinessDetailsComponent {
  @Input() details: any = null;
  formBusiness: FormGroup;
 
  constructor(private fb: FormBuilder,) { 
    this.formBusiness = this.fb.group({
      monthly_income: [''],
    });
  }
  ngOnInit(): void { 
    this.formBusiness.setValue({
      monthly_income: this.details.monthly_income ? this.details.monthly_income :0.00,
  })
  }
  /*
 allow only arrowLeft, arrowRight, ctrl, shit, ctrl + c
 */
 onKeyDown(event: any) {
   const key_cord = event?.keyCode;
   if(key_cord === 17 || key_cord === 16 || key_cord === 35 || key_cord === 36 || key_cord === 37 || key_cord === 39 || key_cord === 67) {
     return true;
   }
   return false;
 }
 formatCommisionRate(rateString: string) {
   return parseFloat(rateString) && rateString;
 }

}
