import { Component } from '@angular/core';
import { CommonModule } from '@angular/common';
import { GenaralDetailsComponent } from './genaral-details/genaral-details.component'; 
import { BankDetailsComponent} from './bank-details/bank-details.component';
import { BusinessDetailsComponent} from './business-details/business-details.component';
import{ ContactPersonDetailsComponent} from './contact-person-details/contact-person-details.component'
import { NbBadgeModule, NbButtonModule, NbCheckboxModule, NbIconModule, NbInputModule, NbMenuModule, NbSelectModule, NbSpinnerModule, NbDatepickerModule, NbProgressBarModule, NbCardModule,NbActionsModule, NbFormFieldModule,NbTabsetModule , NbStepperModule, NbToggleModule,} from '@nebular/theme';
import { ProfileService } from '../../../@core/services';
import { ToastrComponent } from '../../../@core/components';




@Component({
  selector: 'ngx-profile-new',
  standalone: true,
  imports: [CommonModule,GenaralDetailsComponent,BankDetailsComponent,BusinessDetailsComponent,ContactPersonDetailsComponent,NbCardModule],
  templateUrl: './profile-new.component.html',
  styleUrl: './profile-new.component.scss'
})
export class ProfileNewComponent {
  genaral: boolean = true;
  bank: boolean = false;
  business: boolean = false;
  contact_p: boolean = false;
  activeTab: string = 'generalDetails';
  is_loading: boolean = false;
  userDetails: any = null;

 
  
  constructor(private profileService: ProfileService, private toastr: ToastrComponent) { }

  ngOnInit(): void {
    this.getMerchantDetails();
  }
  stepperChange(data:any){
    console.log('data',data)
    if(data=="generalDetails"){
      this.genaral = true
      this.bank = false
      this.business = false
      this.contact_p =false
      this.activeTab = data; 
    }
    else if(data=="bankDetails"){
      this.genaral = false
      this.bank = true
      this.business = false
      this.contact_p =false
      this.activeTab = data; 
    }
    else if(data=="businessDetails"){
      this.genaral = false
      this.bank = false
      this.business = true
      this.contact_p =false
      this.activeTab = data; 
    }
    else if(data=="contactDetails"){
      this.genaral = false
      this.bank = false
      this.business = false
      this.contact_p =true
      this.activeTab = data; 
    }
  }
  getMerchantDetails() {
    this.is_loading = true;

    this.profileService.getMerchantDetails().then((res: any) => {
      console.log('getMerchantDetails444444444444444', res);
      this.is_loading = false;

      if (res?.status === 100) {
        this.userDetails = res?.data;
      }
      else {
        this.toastr.showToast('danger', 'Error!', res?.message || 'Getting merchant details failed.');
      }
    }, (error: any) => {
      this.is_loading = false;
      console.log('Internal Server Error', error);
      this.toastr.showToast('danger', 'Error!', 'Internal Server Error.');
    });

  }

}
