import { Component, OnInit, Input, Output, TemplateRef, EventEmitter } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { NbDialogService } from '@nebular/theme';

@Component({
  selector: 'ngx-contact-person-details',
  standalone: true,
  imports: [],
  templateUrl: './contact-person-details.component.html',
  styleUrl: './contact-person-details.component.scss'
})
export class ContactPersonDetailsComponent {
  @Input() details: any = null;

  ngOnInit(): void { }
   /*
  allow only arrowLeft, arrowRight, ctrl, shit, ctrl + c
  */
  onKeyDown(event: any) {
    const key_cord = event?.keyCode;
    if(key_cord === 17 || key_cord === 16 || key_cord === 35 || key_cord === 36 || key_cord === 37 || key_cord === 39 || key_cord === 67) {
      return true;
    }
    return false;
  }
  formatCommisionRate(rateString: string) {
    return parseFloat(rateString) && rateString;
  }


}
